@import url("https://fonts.googleapis.com/css?family=Roboto:400,700,900");

// General Settings
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1;
  font-family: "Roboto", sans-serif;
  color: #202020;
  background-color: #fafafa;
  font-smooth: always;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul {
  margin: 0;
  padding: 0;
}

// Variables
$generic-box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 0px 1px,
  rgba(0, 0, 0, 0.1) 0px 4px 11px;
$generic-border: 1px solid #f1f1f1;
$generic-bg: #4c956c;

/* Mixins
  ============================================ */
@mixin box-overlay() {
  -webkit-box-shadow: $generic-box-shadow;
  box-shadow: $generic-box-shadow;
  position: absolute;
  width: 97%;
  z-index: 1;
  top: 85px;
  border-radius: 3px;
}

@mixin va($va-align: center) {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;

  @if $va-align != center {
    -webkit-box-pack: left;
    -ms-flex-pack: left;
    justify-content: left;
  } @else {
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
}

@mixin no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@mixin btn($width) {
  width: $width;
  background-color: $generic-bg;
  color: #fff !important;
  border: 1px solid transparent;
  margin-right: 5px;
  margin-top: 10px;
  font-weight: bold;
  font-size: 13px !important;
  line-height: 17px;
  padding: 6px 12px 7px 12px;
  position: relative;
  display: inline-block;
  white-space: nowrap;
  border-radius: 3px !important;
  text-decoration: none !important;
  text-align: center;
  cursor: pointer;
}

@mixin cancel() {
  color: #555;
  cursor: pointer;
  font-size: 14px;
  margin: 2px 5px;
}

@mixin transparent-button() {
  background-color: transparent;
  border: 0;
}

@mixin task-item() {
  list-style-type: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 30px;
  color: #333;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 13px;
  list-style-type: none;
  border-bottom: 1px solid #f0f0f0;
  padding-left: 10px;
  cursor: pointer;

  &:hover {
    background-color: #f9f9f9;
  }
}

@mixin mq($from) {
  @media (max-width: $from) {
    @content;
  }
}

/* Darkmode
  ============================================ */
.darkmode {
  background-color: #252525;

  header {
    -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15);
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15);
    background-color: #282828;
    border-color: transparent;
  }

  .sidebar {
    color: hsla(0, 0%, 100%, 0.87);
    background-color: #252525;
    border-color: #303030;

    &_generic {
      li div {
        color: hsla(0, 0%, 100%, 0.87);
        &.active,
        &:hover {
          background-color: #303030;
        }
        div:nth-child(1) span:first-of-type svg {
          color: hsla(0, 0%, 100%, 0.87);
        }
      }
    }

    &_middle {
      h2 {
        color: hsla(0, 0%, 100%, 0.87);
      }
      span {
        color: #4c956c;
      }
      border-color: #303030;
    }

    &_project {
      &.active,
      &:hover {
        background-color: #303030;
      }
    }

    .add-project {
      &_text,
      &_cancel {
        color: hsla(0, 0%, 100%, 0.87);
      }
      &_plus {
        color: $generic-bg;
      }
      :hover {
        background-color: transparent;
      }
    }
  }

  .project-delete-modal_inner {
    background-color: #363636;
    span {
      color: white;
    }
  }

  .tasks {
    color: hsla(0, 0%, 100%, 0.87);
    background-color: #202020;
    border-color: transparent;

    &_list li {
      border-color: #303030;
      span {
        color: hsla(0, 0%, 100%, 0.87);
      }
    }
  }

  .add-task {
    &_text,
    &_cancel,
    &_date,
    &_project {
      color: hsla(0, 0%, 100%, 0.87);
    }
    &_plus {
      color: $generic-bg;
    }
  }

  .project-overlay_list li div {
    background-color: #303030;
    color: white;
    border-color: #404040;
    &:hover {
      background-color: #404040;
    }
  }

  .task-date {
    &_list li div {
      padding: 0 10px;
      color: white;
      border-color: #404040;
      background-color: #303030;

      span:first-of-type {
        margin-right: 10px;
        svg {
          fill: white;
        }
      }
      &:hover {
        background-color: #404040;
        font-weight: bold;
      }
    }
  }

  .add-task_overlay .add-task_main {
    color: white;
    background-color: #303030;
  }
}

/* Header
  ============================================ */
header {
  border-bottom: solid 1px #4c956c;
  background-color: $generic-bg;
  -webkit-transition: height 200ms ease-in;
  transition: height 200ms ease-in;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
  height: 44px;
  z-index: 400;
  position: fixed;
  top: 0;
  width: 100%;

  @include mq("900px") {
    padding: 0 10px;
  }

  nav {
    display: grid;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    max-width: 922px;
    margin: auto;
    height: 44px;

    p,
    li {
      color: white;
    }

    div {
      &.menu-bar {
        display: flex;
        padding-left: 8px;
        grid-area: 1 / 1;

        svg {
          padding: 3px;
          width: 33px;
          height: 33px;
          fill: white;

          &:hover {
            border-radius: 3px;
            background-color: rgba(255, 255, 255, 0.2);
          }
        }
      }

      &.settings {
        grid-area: 1 / 2;
        text-align: right;

        ul {
          width: 100px;
          display: flex;
          float: right;
          justify-content: space-between;
        }

        ul li {
          @include va;
          float: left;

          div {
            list-style-type: none;
            cursor: pointer;
            width: 30px;
            height: 30px;
            text-align: center;
            vertical-align: middle;

            &:hover {
              border-radius: 3px;
              background-color: rgba(255, 255, 255, 0.2);
            }

            &.settings_add {
              font-size: 30px;
            }

            &.settings_darkmode {
              @include transparent-button();
              color: white;
              cursor: pointer;
              padding-top: 5px;

              &:hover {
                border-radius: 3px;
                background-color: rgba(255, 255, 255, 0.2);
              }

              svg {
                width: 18px;
                height: 18px;
              }
            }

            &.sign-out {
              margin-left: 10px;
              border: solid 2px white;
              border-radius: 50%;
              p {
                margin: auto;
                padding-top: 5px;
              }
            }
          }
        }
      }
    }
  }
}

/* Sidebar
  ============================================ */
.sidebar {
  @include no-select();
  width: 266px;
  height: calc(100vh);
  padding-top: 74px;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  border-right: $generic-border;
  background-color: #fafafa;

  // @include mq("900px") {
  //   display: none;
  // }

  li {
    &:hover .sidebar_project-delete {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
    }
  }

  &_project {
    cursor: pointer;

    div:nth-child(1) {
      padding: 5px 0;
      padding-left: 10px;
      padding-right: 15px;
      width: 100%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
    }

    &-delete {
      margin-left: auto;
      display: none;

      svg {
        color: #cacaca;
      }
    }

    &.active,
    &:hover {
      font-weight: bold;
      background-color: white;
    }

    &:nth-child(1) {
      .sidebar_dot {
        color: #6accbc;
      }
    }
    &:nth-child(2) {
      .sidebar_dot {
        color: #fad003;
      }
    }
    &:nth-child(3) {
      .sidebar_dot {
        color: #ff8d85;
      }
    }
    &:nth-child(4) {
      .sidebar_dot {
        color: #ff9932;
      }
    }
    &:nth-child(5) {
      .sidebar_dot {
        color: #af38eb;
      }
    }
  }

  &_projects {
    padding-left: 3px;
  }

  &_project {
    @include va("left");
  }

  &_dot {
    margin-right: 10px;
    font-size: 30px;
  }

  &_middle {
    margin-top: 20px;
    margin-bottom: 20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    border-bottom: $generic-border;
    padding-left: 10px;
    cursor: pointer;

    span {
      color: #0000008a;
      margin-right: 10px;

      svg.hidden-projects {
        -webkit-transform: rotate(-90deg);
        transform: rotate(-90deg);
      }
    }

    h2 {
      margin: 0;
      color: #333;
      font-size: 15px;
      font-weight: bold;
      padding-bottom: 20px;
    }
  }

  &_generic {
    li {
      list-style-type: none;
      color: #333;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      cursor: pointer;
      line-height: 1;
      font-size: 14px;
      padding-left: 0;
      padding-right: 0;

      div:nth-child(1) {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        padding: 10px 0 10px 10px;
        width: 100%;

        span:first-of-type {
          margin-right: 10px;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
          -ms-flex-align: center;
          align-items: center;

          svg {
            width: 18px;
            height: 18px;
            color: #555;
          }
        }
      }
      div {
        &.active,
        &:hover {
          font-weight: bold;
          background-color: white;
        }
      }
    }
  }
}

/* Show Confirm Delete
  ============================================ */
.project-delete-modal {
  position: absolute;
  margin-top: 20px;
  width: 90%;

  &_inner {
    display: flex;
    flex-direction: column;
    @include box-overlay();
    z-index: 400;
    width: 200px;
    padding: 10px;
    top: 10px;
    right: 0;
    background-color: white;

    p {
      text-align: justify;
      margin-top: 5px;
      margin-bottom: 5px;
      line-height: normal;
      font-weight: normal;
      font-size: 14px;
    }

    button {
      @include btn("50px");
    }
    span {
      @include cancel();
      font-weight: normal;
    }
  }
}

/* Main Content
  ============================================ */
.content {
  display: flex;
  flex-grow: 1;
  max-width: 912px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  grid-template-columns: 1fr auto;
  max-width: 922px;
  margin: auto;
}

/* Tasks
  ============================================ */
.tasks {
  flex-grow: 2;
  width: auto;
  background-color: white;
  border-right: $generic-border;
  min-height: calc(100vh);
  vertical-align: top;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 80px;
  padding-bottom: 84px;
  align-items: center;

  @include mq("900px") {
    margin-left: 0;
    width: auto;
  }

  h2 {
    font-size: 16px;
    font-weight: bold;
    margin: 0 30px 20px 0;
  }

  .add-task {
    margin-top: 20px;
  }

  &_list li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    line-height: 18px;
    color: #333;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 13px;
    list-style-type: none;
    border-bottom: 1px solid #f0f0f0;

    .checkbox-holder {
      margin-right: 20px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      line-height: 16px;

      .checkbox {
        cursor: pointer;
        border-color: gray;
        color: #343434;
        height: 16px;
        width: 16px;
        display: block;
        border: 1px solid gray;
        border-radius: 16px;
      }
    }
  }
}

/* Add Task
  ============================================ */
.add-task {
  &_overlay {
    background: rgba(102, 102, 102, 0.5);
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2000;
    -webkit-transition: opacity 0.2s ease-in;
    transition: opacity 0.2s ease-in;
    height: calc(100vh);

    .add-task_main {
      @include mq("900px") {
        width: 90%;
      }

      overflow: visible;
      margin-bottom: 20px;
      margin-top: 55px;
      border-radius: 5px;
      background-color: white;
      position: absolute;
      left: 50%;
      top: 50%;
      padding: 10px 20px;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);

      .header {
        font-size: 14px;
        margin-bottom: 20px;
        float: left;
      }
    }

    .add-task_cancel-x {
      cursor: pointer;
      color: gray;
      float: right;
      font-weight: bold;
      font-size: 13px;
      margin-top: 10px;
    }
  }

  &_main {
    position: relative;
  }

  &_project,
  &_date {
    cursor: pointer;
    float: right;
    color: gray;
    margin: 20px 10px;
  }

  &_project {
    margin-right: 0;
  }

  &_content {
    width: 100%;
    border: 1px solid #ddd;
    background-color: white !important;
    border-radius: 3px;
    height: 35px;
    padding-left: 10px;
    padding-right: 10px;
  }

  &_cancel,
  &_submit {
    @include no-select;
  }

  &_cancel {
    @include cancel();
  }

  &_submit {
    @include btn("90px");
  }
}

.add-task_shallow,
.add-project {
  @include no-select;
  cursor: pointer;
  margin-bottom: 20px;
}

.add-task_plus,
.add-project_plus {
  color: $generic-bg;
  font-size: 18px;
}

.add-task_text,
.add-project_text {
  display: inline-block;
  color: #545454;
  font-size: 14px;
  margin-left: 15px;
}

/* Task Date
  ============================================ */
.task-date {
  @include box-overlay();
  background-color: #fff;
  width: 300px;
  right: 0;

  &_list li {
    list-style-type: none;
  }
  div {
    @include task-item();
    padding: 0 10px;
    list-style: none;

    span:first-of-type {
      margin-right: 10px;
      svg {
        fill: grey;
      }
    }
    &:hover {
      font-weight: bold;
    }
  }
}

/* Project Overlay
  ============================================ */
.project-overlay {
  @include box-overlay();
  background-color: white;
  width: 300px;
  right: 0;

  @include mq("900px") {
    width: 200px;
    right: 0;
  }

  &_list li {
    list-style: none;
  }
  div {
    @include task-item();
    padding: 0 10px;

    div:nth-child(1) {
      width: 50%;
      padding: 10px;
    }
    &:hover {
      font-weight: bold;
    }
  }
}

/* Add Project
  ============================================ */
.add-project {
  padding-left: 13px;
  padding-right: 20px;
  margin-top: 20px;

  &_input {
    margin-bottom: 20px;
  }

  &_name {
    width: 100%;
    border: 1px solid #ddd;
    background-color: white !important;
    border-radius: 3px;
    height: 35px;
    padding-left: 10px;
    padding-right: 10px;
  }

  &_submit {
    @include btn("100px");
  }

  &_cancel {
    @include cancel();
  }

  &_text,
  &_plus {
    &:hover {
      font-weight: bold;
      background-color: white;
    }
  }
}

// login and signup form

.login-wrapper {
  width: 400px;
  height: fit-content;
  margin: auto;
  div {
    width: 90%;
    margin: 1% 0 1% 0;
    align-items: center;
    h3 {
      font-size: 14px;
    }
    &.helper-block {
      color: rgba(102, 102, 102, 0.5);
      display: flex;
      flex-direction: column;
      font-size: 14px;
    }
  }
  .full-logo {
    display: flex;
    align-items: flex-start;
    margin-top: 5%;
    img {
      width: 120px;
    }
  }
  form {
    @include box-overlay();
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: fit-content;
    width: 400px;
  }

  input {
    width: 100%;
    border: 1px solid #ddd;
    background-color: white !important;
    border-radius: 3px;
    height: 35px;
    padding-left: 10px;
    padding-right: 10px;
  }

  button {
    @include btn("50px");
    width: 100%;
  }
  .alert_error {
    color: red;
    font-size: 14px;
  }
  .alert_message {
    color: black;
    font-size: 14px;
  }
}

// user settings
.user-settings {
  position: inherit;
  display: flex;
  flex-direction: row-reverse;

  &_inner {
    @include box-overlay();
    height: 64px;
    z-index: 400;
    width: 200px;
    padding: 2px;
    top: 44px;
    background-color: white;
    font-size: 13px;

    div {
      &.logout {
        &:hover {
          font-weight: bold;
          background-color: rgb(216, 216, 216);
          cursor: pointer;
        }
      }

      display: flex;
      align-items: center;
      height: 30px;
      padding-left: 5px;
    }
    p {
      padding-left: 10px;
      color: black;
    }
  }
}

//loading
.loading {
  height: 400px;
  color: #4c956c;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}